body {
  font-family: Work Sans, sans-serif;
}

header {
  width: 100%;
  background-color: #fff;
}

header .container {
  height: 8rem;
  justify-content: space-between;
  align-items: center;
  padding-left: 3rem;
  padding-right: 3rem;
  display: flex;
}

.item {
  border: 1px solid #000;
  padding: 1rem;
}

.cart i {
  font-size: 2rem;
}

.items {
  list-style: none;
}

.container {
  background-color: #e1e1e1;
}

.container .items {
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  display: grid;
}

.container .item {
  background-color: #fff;
  padding: 30px;
}

.container .item .item_img {
  width: 50%;
  margin-bottom: 20px;
}

.container .item h4 {
  color: #666;
  font-size: 14px;
}

.container .item p {
  color: #666;
  margin-top: 10px;
  font-size: 14px;
  line-height: 22px;
}

.add_cart {
  cursor: pointer;
}

.cart {
  text-decoration: none;
  position: relative;
}

.quantity-cart {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1rem;
  position: absolute;
  top: -18px;
  right: -15px;
}

.sold-out {
  pointer-events: none;
  opacity: .5;
  border: 5px solid red;
}

@media (width <= 800px) {
  .container .items {
    grid-template-columns: repeat(1, 1fr);
  }
}

